@import '~antd/dist/antd.css';

.header{
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 3;
}

.ant-layout-sider{
  height: calc(100% - 64px);
  position: fixed;
  left: 0;
  top:64px;
  overflow: auto;
  z-index: 3;
}

.ant-layout-footer{
  width: calc(100% - 200px);
  padding:16px;
}

#components-layout-demo-top .logo {
  width: 120px;
  height: 31px;
  background: rgba(255, 255, 255, 0.2);
  margin: 16px 24px 16px 0;
  float: left;
}